%active-star {
  background-image: url(../images/star-active.svg);
}

.stars {
  display: flex;

  &__star {
    margin-right: 4px;
    background-image: url(/images/star.svg);
    background-repeat: no-repeat;
    background-position: 50%;
    width: 16px;
    height: 16px;

    &:last-child() {
      margin-right: 0;
    }
  }

  &--4 &__star:nth-child(-n+4) {
    @extend %active-star;
  }
}
